import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { SaaratrendsComponent } from './saaratrends/saaratrends.component';
import { TestcodeComponent } from './testcode/testcode.component';
import { SignUpComponent } from './sign-up/sign-up.component';


const routes: Routes = [
  {path:'home',component:SaaratrendsComponent},
  {path:'test',component:TestcodeComponent},
  {path:'sign_up',component:SignUpComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }

export const all_links=[SaaratrendsComponent,TestcodeComponent,SignUpComponent]